<template>
  <div class="ranking">
    <h1>RANKING</h1>
    <div class="data">
      <v-data-table :headers="headers" :items="users" :items-per-page="50" dark hide-default-footer></v-data-table>
    </div>
  </div>
</template>

<script>
import { db } from '../plugins/firebase';

export default {
  data() {
    return {
      users: [],
      headers: [
        {
          text: "Player",
          value: "name",
          align: "center",
          sortable: false,
        },
        {
          text: "Power Eggs",
          value: "power_eggs",
          align: "center",
          sortable: true,
        },
        {
          text: "Golden Eggs",
          value: "golden_eggs",
          align: "center",
          sortable: true,
        },
        {
          text: "Asphalt",
          value: "asphalt",
          align: "center",
          sortable: true,
        },
        {
          text: "Score",
          value: "type",
          align: "center",
          sortable: true,
        },
      ]
    }
  },
  async mounted() {
    let url = "https://salmonia.mydns.jp/api/users"
    let response = await fetch(url)
    let users = await response.json()

    console.log(users)
    users.forEach(user => {
      // if (user["name"])
      // ユーザが存在してないときは追加する
      db.collection("asphalt").doc(user).set(
        {
          name: user,
          // golden_eggs: 0,
          // power_eggs: 0,
          // asphalt: 0,
        },
        { merge: true }
      )
    });
    // console.log(json)
  },
  firestore() {
    return {
      users: db.collection("asphalt")
    }
  }
}
</script>

